.section-skills{
    margin: 60px 0;
}
.skills{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    max-width: 1300px;
    margin: 20px 40px;
}
.hidden{
    display: none;
}

#idlearning{
    opacity: .7;
}

.section-skills .show-btn span{
    color: #012169;
    font-size: 1.3rem;
    font-style: italic;
}

.section-skills .show-btn span:hover{
    color: #15cfcf;
    cursor: pointer;
}
