.Education {
    padding: 30px;
}

.Education .row div {
    min-height: 350px;
    padding: 30px;
}

.edu {
    border: solid 1px;
    text-align: center;
    transition: all 0.3s ease-in;
}

.edu:hover {
    background-color: #191937;
    color: white;
}

.edu i {
    background-color: #012169;
    border-radius: 50%;
    color: #fff;
    font-size: 2rem;
    padding: 9px 15px;
}

.edu i:hover {
    background-color: #06d0d0;
}

.edu h4 {
    padding: 20px 0;
}

.edu p {
    font-size: 1.1rem;
    margin-bottom: 25px;
    padding: 0 25px;
}

.cert {
    text-decoration: underline;
}

.cert:hover {
    color: #06d0d0;
    cursor: pointer;
}

.edu .badge {
    font-size: 1rem;
}