.Projects {
    padding-bottom: 30px;
    margin: 0 15px;
}

.card {
    border: none;
    background-color: transparent;
}

.Projects .slider {
    margin: 30px;
}

.Projects .goto {
    text-align: center;
}

.Projects .goto span {
    text-align: center;
    font-size: 20px;
}

.Projects .goto button:hover {
    background-color: #7affff;
}

.slick-prev:before,
.slick-next:before {
    color: #16113c !important;
}