.card2 {
    border: none;
}

.card2 .content2:hover {
    cursor: pointer;
}

.card2 .content2:hover .overlay {
    opacity: 1;
}

.card2 .content2:hover .image2 {
    scale: 1.1;
    opacity: .2;
}

.card2 .content2 .image2 {
    margin: auto;
    width: 90%;
    transition: all 0.3s ease;
}

.card2 .content2 .overlay {
    transition: all 0.3s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.card2 .content2 .overlay p {
    font-size: 2rem;
    color: #191937;
}

.card2 .content2 .overlay .icons {
    display: flex;
    justify-content: center;
}

.card2 .content2 .overlay .icons i {
    font-size: 3rem;
    margin: 20px;
    color: #191937;
}

.card2 .content2 .overlay .icons i:hover {
    color: #00b8d0;
}

.card2 .content2 .image2:hover {
    margin: auto;
    transition: all 0.3s ease;
}

/*Responsive*/ 
@media screen and (max-width: 767px) {
    .card2 .content2 .overlay p{
        font-size: 1.4rem;
    } 

    .card2 .content2 .overlay .icons i {
        font-size: 1.8rem;
    }
}
