section .enlaces {
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
}

section a {
    font-size: 40px;
    color: #16113c;
    margin: 20px 30px;
}

section a:hover {
    color: #00dada;
    scale: 1.2;
}

.cv-div {
    text-align: center;
    margin: 30px 0 30px 0;
}

.cv-div p {
    font-size: 1.1rem;
    background-image: linear-gradient(-225deg,
            #031370 0%,
            #031370 39%,
            #95cbf7 59%,
            #031370 77%,
            #0f0221 100%);
    background-size: auto auto;
    background-clip: border-box;
    background-size: 200% auto;
    color: #fff;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: textclip 1s linear infinite;
}

@keyframes textclip {
    to {
        background-position: 200% center;
    }
}


#cv {
    width: 130px;
}

#cv:hover {
    scale: 1.1;
    animation: 12s;
}