@import url('https://fonts.googleapis.com/css2?family=Space+Mono:ital@1&display=swap');

section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

h1 {
    font-size: 3.5rem;
    font-family: 'Orbitron', sans-serif;
}


.section-dark {
    background: linear-gradient(180deg, #16113c, #04193e);
    color: #f5f6f7;
}

.section-title {
    font-size: 2.5rem;
    padding: 30px;
}

.section-text {
    font-family: 'Space Mono', monospace;
    font-size: 1.3rem;
    margin: 50px 0;
}

.section-text .onelink {
    font-size: 1.3rem;
    margin: 10px 0;
    color: #ffffff;
}

.btn-info {
    font-size: 1rem;
    margin: 30px;
}

/*Responsive*/
@media screen and (max-width: 767px) {
    h1 {
        font-size: 2.5rem;
    }

    .section-title {
        font-size: 2.1rem;
        padding: 30px;
    }

    .section-text {
        font-size: 1rem;
    }

    .section-text .onelink {
        font-size: 1rem;
    }
}