.jb-div {
    max-width: 60%;
    margin-bottom: 40px;
}

.jb-div h4 {
    color: #191154;
}

.jb-div h4:hover {
    color: #05b9dd;
}

.jb {
    margin: 60px 0;
    font-size: 1.2rem;
    text-align: justify;
}

.pc {
    list-style-type: circle;
}

/*resposive*/
@media screen and (max-width: 767px) {
    .jb-div {
        max-width: 75%;
    }

    .jb {
        font-size: 1rem;
    }

    .jb-div h4 {
        font-size: 1.1rem;
    }

}